import "core-js/modules/es.array.includes.js";
import "core-js/modules/es.object.to-string.js";
import "core-js/modules/es.string.includes.js";
import axios from 'axios';
import { Notification, MessageBox, Message } from 'element-ui';
import store from '@/store';
import { getToken } from '@/utils/auth';
import errorCode from '@/utils/errorCode';
axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8'; // axios

var service = axios.create({
  // axiosbaseURLURL
  baseURL: process.env.VUE_APP_BASE_API,
  // 
  timeout: 10000
}); // request

service.interceptors.request.use(function (config) {
  //  token
  var isToken = (config.headers || {}).isToken === false;

  if (getToken() && !isToken) {
    config.headers['Authorization'] = 'Bearer ' + getToken(); // token 
  }

  return config;
}, function (error) {
  console.log(error);
  Promise.reject(error);
}); // 

service.interceptors.response.use(function (res) {
  // 
  var code = res.data.code || 200; // 

  var msg = errorCode[code] || res.data.msg || errorCode['default'];

  if (code === 401) {
    MessageBox.confirm('', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }).then(function () {
      store.dispatch('LogOut').then(function () {
        location.href = '/index';
      });
    });
  } else if (code === 500) {
    Message({
      message: msg,
      type: 'error'
    });
    return Promise.reject(new Error(msg));
  } else if (code !== 200) {
    Notification.error({
      title: msg
    });
    return Promise.reject('error');
  } else {
    return res.data;
  }
}, function (error) {
  console.log('err' + error);
  var message = error.message;

  if (message == "Network Error") {
    message = "";
  } else if (message.includes("timeout")) {
    message = "";
  } else if (message.includes("Request failed with status code")) {
    message = "" + message.substr(message.length - 3) + "";
  }

  Message({
    message: message,
    type: 'error',
    duration: 5 * 1000
  });
  return Promise.reject(error);
});
export default service;